import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '@/App.vue'

// 把这段代码直接粘贴到router/index.js中的Vue.use(VueRouter)之前
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function (location) {
  return originalPush.call(this, location).catch(err => { })
};
// 捕获报错 进行了隐藏，所以浏览器看不到
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: "/caidan",
    name: 'index',
    component: App
  },
  {
    path: "/tree",
    name: 'tree',
    component: () => import("@/components/tree.vue")
  },
  {
    path: '/app',
    name: 'app',
    component: () => import('@/components/app.vue')
  },
  {
    path: '/home',
    name: 'home',
    component: () => import('@/views/HomeView.vue')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('@/views/About.vue')
  },
  {
    path: '/aaa',
    name: 'aaa',
    component: () => import('@/components/aaa.vue')
  },
  {
    path: '/zujian',
    name: 'zujian',
    component: () => import('@/components/zujian.vue')
  },
  {
    path: '/cheshi',
    name: 'cheshi',
    component: () => import('@/components/cheshi.vue')
  },
  {
    path: '/caidan',
    name: 'caidan',
    component: () => import('@/components/caidan.vue')
  },
  {
    path: '/echarts',
    name: 'echarts',
    component: () => import('@/components/echarts.vue')
  },
  {
    path: '/scrollto',
    name: 'scrollto',
    component: () => import('@/components/scrollto.vue')
  },
  {
    path: '/iframepdf',
    name: 'iframepdf',
    component: () => import('@/components/iframepdf/index.vue')
  },
  // 所有未定义路由，全部重定向到404页，必须放在最后
  {
    path: '*',
    redirect: '/404',

  }, {
    path: '/404',
    component: () => import('@/components/empty/index.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// router.beforeRouteLeave(to, from, next){
//   // 手动清除组件的缓存
//   this.$options.destroyed = true;
//   next();
// }

export default router
