<template>
  <div id="app">
    <!-- 上面 -->
    <el-container  v-if="false">
         <el-header style="border: 1px solid #000;height: 60px;">
            <h2 style="line-height: 60px;">XXX后台系统</h2>
          </el-header>
    </el-container>
    <!-- 下面 -->
    <el-container  v-if="false">
          <!-- 左边 -->
                     <el-menu
                          :collapse="isCollapse"
