                          class="el-menu-vertical-demo"
                          >
                    <!-- @open="handlezOpen" -->
                    <!-- @close="handlezClose" -->   
                                  <el-button type="primary" round size="mini" @click="isCollapse=!isCollapse">{{isCollapse?'展开':'收起'}}</el-button>
                          <el-menu-item index="0">
                           <i class="el-icon-menu"></i>
                          <span slot="title">首页</span>
                          </el-menu-item>
                          <el-submenu index="1">
                            <template slot="title">
                              <i class="el-icon-location"></i>
                              <span>导航一</span>
                            </template>
                            <el-menu-item 
                            v-for="item in caidan1"
                            :index="item.url">{{ item.title }}</el-menu-item>
                            <!-- <el-menu-item index="about">about</el-menu-item> -->
                            <!-- <el-menu-item index="1-3">选项3</el-menu-item> -->
                          </el-submenu>
                          <el-submenu index="2">
                              <template slot="title">
                               <i class="el-icon-menu"></i>
                               <span>导航二</span>
                              </template>
                            <el-menu-item 
                            v-for="item in caidan2"
                            :index="item.url">{{ item.title }}
                            </el-menu-item>
                          </el-submenu>
                          <el-submenu index="3">
                              <template slot="title">
                               <i class="el-icon-menu"></i>
                               <span>导航三</span>
                              </template>
                            <el-menu-item 
                            v-for="item in caidan3"
                            :index="item.url">{{ item.title }}
                            </el-menu-item>
                          </el-submenu>
                   </el-menu>
          <!-- 右边 -->
           <el-main style="height: calc(100vh - 60px);padding: 0;">
               <div class="tab-container">
                      <!-- 动态渲染标签页 -->
                      <div>
                          <el-tag class="tab"
                          @click="handleClick('app')"
                          :effect="$route.path.slice(1)=='app'?'dark':'light'"
                          >首页</el-tag>
                          <el-tag   
                          v-for="(tab, index) in tabs"
                          :key="index"
                          class="tab"
                          closable
                          :effect="$route.path.slice(1)==tab?'dark':'light'"
                          @click="handleClick(tab)"
                          @close.stop="handleClose(tab)">
                          {{ urltitle(tab) }}</el-tag>
                      </div>
               </div>
               <!-- 下面的路由出口 -->
               <keep-alive>
                    <router-view />
              </keep-alive>
                   
          </el-main>
    </el-container>
    <router-view />
  </div>
</template>

<script>
 export default {
    name:"app",
    data() {
      return {
        isCollapse: true,
        indexkey:"0", //初始haul高亮
        tabs: [], // 标签页列表
        caidan1:[{
          url:'home',
          title:'菜单1-1'
        },
        {
          url:'about',
          title:'菜单1-2'
        }],
        caidan2:[
        {
          url:'aaa',
          title:'菜单2-1'
        },
        {
          url:'zujian',
          title:'菜单2-2'
        }
        ],
        caidan3:[
        {
          url:'cheshi',
          title:'菜单3-1'
        },
        {
          url:'caidana',
          title:'菜单3-2'
        }
        ]
      };
      // 添加新菜单的注意点
      // 1.在路由里面配置好组件,
      // 2.在APP的data里面添加菜单,
      // 3.在右边菜单栏里面添加新的菜单
      // 4.在tabs渲染的标签的方法中添加上新的菜单标题
    },
    methods: {
      // 点击菜单
      // handlezOpen(key, keyPath) {
      //   console.log(key, keyPath);
      // },
      // 点击收起菜单
      // handlezClose(key, keyPath) {
      //   console.log(key, keyPath);
      // },
      // 根据菜单项的索引添加对应的标签页   点击子菜单触发
      handleMenuSelect(index) {
        console.log(index);
        console.log(this.tabs);
        this.indexkey=index
        if (index=="0") {
          this.$router.push(`/app`)
          return
        }
        this.$router.push(`/${index}`)
        let ind = 0  //遍历上面的tabs里面有没有这个元素
        this.tabs.forEach(itme=>{
          if (itme.includes(index)) {
             ind ++
          }
        })
        if (ind!=0) {
          return
        }
        this.tabs.push(`${index}`);
        console.log(index);
        console.log(this.tabs);

      },
      //点击tab时触发的事件
      handleClick(tag){
         //点击tabs，让菜单高亮+跳转
        if (tag=='app') {
            this.$router.push('/app') 
            this.indexkey='0'      
        }else{
          this.$router.push(`/${tag}`) 
          this.indexkey=tag  
        }

      },
      //点击删除对应菜单 高亮的判断  
      handleClose(tag) {
        console.log(this.$route.name==tag);
      if (this.$route.name==tag?false:true) {
     console.log('测试1',tag,this.tabs);
       // 删除其他高亮，获取到点击的下标，在tabs里面把它删了
           let index = this.tabs.indexOf(tag)
           this.tabs.splice(index,1)
                console.log(this.tabs);
             return
          }

        // 这边是不同删除页面的跳转(高亮标签删除)
        if(this.tabs.length==1){
          this.indexkey='0'
          this.$router.push('/app')
          console.log('测试2',tag,this.tabs);
        }
        else if(this.tabs[this.tabs.length-1]==tag){
          this.indexkey=`${this.tabs[this.tabs.indexOf(tag)-1]}`
          this.$router.push(`/${this.tabs[this.tabs.indexOf(tag)-1]}`)
          console.log('测试3',tag,this.tabs);
        }
        else 
        if (this.tabs.length>1) {
          console.log('大于二的时候',this.tabs.indexOf(tag)+1);
          this.indexkey=`${this.tabs[this.tabs.indexOf(tag)+1]}`
          this.$router.push(`/${this.tabs[this.tabs.indexOf(tag)+1]}`)
          console.log('测试4',tag,this.tabs);
        }
        else{
               alert('测试')
        }
        this.tabs.splice(this.tabs.indexOf(tag), 1);
        console.log(this.tabs);
      },
      //修改菜单title展示的数据
      urltitle(tag){
        let title = [...this.caidan1,...this.caidan2,...this.caidan3]   
        //过滤，url是一样的就返回，但是是数组，所以需要下标
       return  (title.filter(item=>item.url==tag))[0].title
      }
    },
    created(){
     
      this.indexkey="0", //初始haul高亮
      this.tabs= [] //
      if (this.tabs = []) {
                 
      }
    },
    watch:{
          // 监听 $route 对象的变化
        
        '$route': {
          handler(newRoute, oldRoute) {
            // 路由发生变化时的处理逻辑
            // this.indexkey=newRoute
            // console.log('路由变化啦！', newRoute, oldRoute);
            // 执行其它操作或调用方法
          }
        }
    }
  }
</script>

