import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
//借阅病历弹出框组件
import Dialog from '@/components/Dialog'
//全局使用的js插件
import ExtendFn from '@/plugins/extend'
//全局自定义指令
import Directive from '@/directives/index'
//这行是提示开发模式和发布（生产）模式，本行代码无意义
Vue.config.productionTip = false
//右键菜单的插件
import VueContextMenu from 'vue-contextmenu'
import Print from 'vue-print-nb'

Vue.use(Print);

/*element的依赖*/
/*引入下面三行*/
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);


// antDesign全局的依赖
import 'ant-design-vue/dist/antd.css'; // or 'ant-design-vue/dist/antd.less'
import antDesign from 'ant-design-vue';
Vue.use(antDesign);

//全局组件
Vue.component('Aaaa', Dialog)


// 全局控制dialog遮罩层是否点击关闭变量
Vue.prototype.closeModalDialog = false

//全局注册的插件
Vue.use(ExtendFn); //自定义弹框js调用的方法
Vue.use(Directive) //自定义指令
Vue.use(VueContextMenu)//右键的菜单

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
