<template>
    <div>
   <el-dialog :visible.sync="dialogVisible" title="表单弹窗" :close-on-click-modal="closeModalDialog" @close="handleClose">
    <!-- 表单内容 -->
    <el-form ref="formRef" :model="form" label-width="120px" :rules="rules">
          <el-form-item label="借阅目的" prop ="purpose" >
                  <el-input
                    size="mini"
                    v-model="form.purpose"
                    type="textarea"
                    :rows="2"
                    maxlength="200"
                    placeholder=""
                  />
          </el-form-item>
          <el-form-item label="患者ID" prop ="patientid" >
                  <el-input
                    size="mini"
                    v-model="form.patientid"
                    placeholder=""
                  />
          </el-form-item>
          <el-form-item label="预计归还时间" prop ="yjghtime" >
                  <el-date-picker
                    size="mini" 
                    v-model="form.yjghtime"
                    placeholder="请选择日期"
                    type="date"
                    value-format="yyyy-MM-dd"
                    align="right"
                  >
                  </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button @click="submitForm('formRef')">提交</el-button>
          </el-form-item>
</el-form>
  </el-dialog>
    </div>
</template>

<script>
export default {
  name: "Dialog",
  data() {
    return {
      dialogVisible:false,   //控制显示隐藏
      dialogpatientid:null,  //病人id
      // 定义表单字段
       form:{
           purpose:null,
           patientid:null,
           yjghtime:null
       },
       rules:{
        purpose: [{ required: true, message: '请输入借阅目的', trigger: 'blur' }],
        patientid: [{ required: true, message: '请输入患者id', trigger: 'blur' }],
        yjghtime: [{ required: true, message: '请选择日期', trigger: 'blur' }]
       }
    }
  },
  props: ["visible","patientid"],
  //监听属性
  watch: {
    visible(newValue) {
      this.dialogVisible = newValue; // 监听props的变化，更新子组件的data属性
    },
    patientid(newValue){
        this.dialogpatientid = newValue
        this.form.patientid = this.dialogpatientid
        console.log(this.form.patientid);
    }
  },
  methods: {
    handleClose() {
  
       // 关闭弹出框触发
       this.$emit("update:visible", this.dialogVisible); // 通过事件将修改后的值传递给父组件
       this.$refs.formRef.clearValidate()// 取消验证
    },
    
    submitForm(formName) {
      // 提交表单操作
      // ...
      this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
            console.log(this.form);
              // 关闭弹出框
                //  console.log('提交1');
                 this.dialogVisible=false
                 this.$emit('update:visible',this.dialogVisible);

           this.form={
           purpose:null,
           patientid:null,
           yjghtime:null
                }
          console.log(this.form);
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    
    },
   resetForm(formName) {
        this.$refs[formName].resetFields();
      },
  }
}
</script>
 
<style lang = "scss" scoped>
    
</style>