import Vue from 'vue';
import { Message } from 'element-ui';
//传参(组件,data,methdes,watch)
const ExtendFn = (componets, data, methods, watch) => {
    // new 一个实例
    const instance = new (Vue.extend(componets))(
        {
            data,// 存放数据
            methods, //存放方法
            watch  //监听
        }
    ).$mount();//将实例进行挂载
    document.body.appendChild(instance.$el);
}

//非空效验框----在使用的方法出直接调用
const MessageFn = () => {
    Message.error('请输入内容再点击确认');
}
// Message.error('错了哦，这是一条错误消息');

export { ExtendFn, MessageFn }


// 注册一个插件
const plugin = {
    install(Vue) {
        // 把toast方法 作为Vue的实例方法存在
        Vue.prototype.$ExtendFn = ExtendFn
        // 在组件中用this.$ExtendFn()来使用ExtendFn函数
    }
}
export default plugin


// 使用方法:
// 1、在使用的文件中默认导入ExtendFn方法并引入组件
